<template>
    <div class="choose_type">
        <ul class="type_list">
            <li v-for="(item,index) in typeList" :key="index" @click="to_register(item.id)">
                {{item.name}}
            </li>
        </ul>
    </div>
</template>
<script>
export default {
    name: 'chooseTypePage',
    data() {
        return {
            typeList: [
                {
                    name: '我要下单',
                    id: 0
                },
                {
                    name: '我要接单',
                    id: 1
                }
            ]
        }
    },
    methods: {
        to_register(id) {
            let code = this.$route.query.code;
            // id == 0 => 跳转用户注册 
            // id == 1 => 跳转师傅注册 
            if(id == 0) {
                this.$router.push(`/userRegister?code=${code}`);
            }else if(id == 1) {
                this.$router.push(`/masterRegister?code=${code}`);
            }
        }
    },
    // beforeRouteLeave(to, from, next) {
    //     // 设置下一个路由的 meta
    //     to.meta.keepAlive = false;  // 让 userRegister 缓存，即不刷新
    //     next();
    // }
}
</script>
<style lang="less" scoped>
@import "../../public/common.less";
.choose_type {
    position: relative;
    width: 100%;
    height: 100%;
    background: url('../../../static/image/choose_type.png') no-repeat;
    background-size: cover;
    .type_list {
        position: absolute;
        left: 125px;
        top: 850px;
        width: 500px;
        height: 240px;
        li {
            background: url('../../../static/image/choost_btn.png') no-repeat;
            background-size: cover;
            height: 100px;
            text-align: center;
            line-height: 100px;
            color: #F75C4D;
            font-size: 30px;
        }
    }
}
</style>



