<template>
    <div class="user_two">
        <div class="user_info">
            <div class="invitate_code">
                <span>推荐人:</span>
                <input type="text" placeholder="邀请码" disabled v-model="user.beinvitecode">
            </div>
            <div class="choose_radio">
                <span>性质:</span>
                <select name="" id="" v-model="user.userType">
                    <option v-for="(item,index) in userTypeList" :key="index" :value="item.Id">{{item.UserTypeName}}</option>
                </select>
            </div>
            <div class="choose_radio" @click="showLocation">
                <span>所在区域:</span>
                <span>{{locationName}}</span>
            </div>
            <div class="ele">
                <input type="text" maxlength="20" v-model.trim="user.userName" placeholder="请输入您的用户名(必填)">
            </div>
            <div class="ele">
                <input type="text" maxlength="11" v-model.trim="user.phone" placeholder="请输入您的手机号(必填)">
                <button class="get_code" @click="getCode" v-if="!showTime">{{getCodeText}}</button>
                <button class="get_code" v-else>{{getCodeText}}</button>
            </div>
            <div class="ele">
                <input type="text" maxlength="6" v-model.trim="user.code" placeholder="请输入您的验证码(必填)">
            </div>
            <div class="ele">
                <input type="password" v-model.trim="user.password" placeholder="请输入您的密码(必填)">
                <img class="delete_btn" @click="deletePass" src="../../../static/image/delete.png" alt="图片">
            </div>
            <div class="ele">
                <input type="password" v-model.trim="repeatPass" placeholder="请确认您的密码(必填)">
                <img class="delete_btn" @click="deleteRepass" src="../../../static/image/delete.png" alt="图片">
            </div>
            <label for="agree" class="user_agree" >
                <input type="checkbox" id="agree" @click="changeChecked(checked)" v-model="checked">阅读并同意
                <router-link to="/userRegister/userService">《微活平台协议》</router-link>
             </label>
        </div>
        <p class="complete_btn" v-if="compleTxt == '注册中...'" >{{compleTxt}}</p>
        <p class="complete_btn" v-else @click="completeReg">{{compleTxt}}</p>
        <citySelect :isShowLocation = isShowLocation 
                    v-on:hideLocationHandleA="hideLocationEventA"
                    v-on:hideLocationHandleB="hideLocationEventB"
                    :nullObj = 'nullObj'
        ></citySelect>
        
    </div>
</template>
<script>
import qs from 'qs';
import citySelect from '@/components/citySelect'
import md5 from 'js-md5';
import common from "../../public/common.js";  //引入封装的公共方法
import api from "@/http/api.js";
export default {
    name: 'userRegisterPage',
    components: {
        citySelect
    },
    data() {
        return {
            isShowLocation: false,                    //是否显示省市区             
            locationName: '请选择您所在的省市区',       //省市区
            showTime: false,
            time: 60,                                 //倒计时时间
            getCodeText: '获取验证码',
            provinceText: '请选择省市区',
            compleTxt: '注册',
            userTypeList: [
                {Id: 1,UserTypeName: "个人"}    //注册性质
            ],
            repeatPass: '',                     //重复密码
            checked: false,                      //微活协议
            user: {
                userType: '',                   // 0 => 个人 1 => 商家
                beinvitecode: '',               //邀请码
                userName: '',                   //用户名
                phone: '',                      //电话
                code: '',                       //验证码
                password: '',                   //密码,
                Province: '',                   //省
                City: '',                       //市
                Area: ''                        //区
            },
            info: {
                sign:'431104c0-984c-4415-90ed-847938e5cb00'
            },
            nullObj: {},                         //清空省市区
            errorTxt: ''
        }
    },
    created() {
        this.user.beinvitecode = this.$route.query.code;
        this.user.userType = this.userTypeList[0].Id;
        this.getUserType();
    },
    methods: {
        hideLocationEventA() {
            this.isShowLocation = false
        },
        hideLocationEventB(a,b) {
            this.nullObj = b;
            this.isShowLocation = a
            this.locationName = ''
            for(var i in b) {
                this.locationName += ' '+b[i];
            }
            this.user.Province = b.Province
            this.user.City = b.City
            this.user.Area = b.Area
        },
        showLocation() {    //省市区选择
            this.isShowLocation = true;
            for(var i in this.nullObj) {
                delete(this.nullObj[i])
            }
        },
        getUserType() {
            let info = {
                sign: ''
            },
            _that = this;
            info.sign = md5(common.changeString({},'431104c0-984c-4415-90ed-847938e5cb00')).toUpperCase();
            console.log(info);
            api.getUserType(info)
            .then(res => {
                if(res.data.header.rspCode == 0) {
                    _that.userTypeList = res.data.body.user_type;
                }
                if(res.data.header.rspCode == 401) {
                    _that.layer.open({
                        content: res.data.header.rspMsg
                        ,skin: 'msg'
                        ,time: 2 //2秒后自动关闭
                    })
                }
            }).catch(err => {
                console.log(err);
            })
        }, 
        deletePass() {
            this.user.password = ''
        },
        deleteRepass() {
            this.repeatPass = ''
        },
        getCode() {
            let info = {
                phone: this.user.phone
            },
            _that = this;
            info.sign = md5(common.changeString(common.objKeySort(info),'431104c0-984c-4415-90ed-847938e5cb00')).toUpperCase();

            //验证所填手机号
            if(!this.user.phone) {
                this.layer.open({
                    content: '手机号码不能为空!'
                    ,skin: 'msg'
                    ,time: 2 //2秒后自动关闭
                })
                return 
            }
            if(!common.checkPhone(this.user.phone)) {
                this.layer.open({
                    content: '手机号码格式不正确!'
                    ,skin: 'msg'
                    ,time: 2 //2秒后自动关闭
                })
                return
            }
            this.getCodeText = '发送中...'
            this.showTime = true
           //发送请求

            api.sendRegisterSms(info)
            .then(res => {
                if(res.data.header.rspCode == 0) {
                    let timer = setInterval(() =>{
                        _that.time--;
                        _that.getCodeText = _that.time+'S后重新获取'
                        if(_that.time < 1) {
                            _that.time = 60
                            _that.showTime = false
                            _that.getCodeText = '重新获取验证码'
                            clearInterval(timer)
                        }
                    },1000)
                }
                if(res.data.header.rspCode == 401) {
                    _that.layer.open({
                        content: res.data.header.rspMsg
                        ,skin: 'msg'
                        ,time: 2 //2秒后自动关闭
                    })
                    _that.getCodeText = '获取验证码'
                    _that.showTime = false
                }
            }).catch(err => {
                console.log(err);
            })
        },
        // 勾选用户协议
        changeChecked(item) {
            if(item) {
                item = false
            }else {
                item = true
            }
        },
        // 点击完成按钮
        completeReg() {
            console.log(this.user);
            if(!this.user.Province || !this.user.City || !this.user.Area) {
                this.layer.open({
                    content: '请选择省市区!'
                    ,skin: 'msg'
                    ,time: 2 //2秒后自动关闭
                })
                return
            }
            if(!this.user.userName) {
                this.layer.open({
                    content: '用户名不能为空!'
                    ,skin: 'msg'
                    ,time: 2 //2秒后自动关闭
                })
                return
            }
            if(!this.user.phone) {
                this.layer.open({
                    content: '手机号码不能为空!'
                    ,skin: 'msg'
                    ,time: 2 //2秒后自动关闭
                })
                return
            }
            if(!this.user.code) {
                this.layer.open({
                    content: '验证码不能为空!'
                    ,skin: 'msg'
                    ,time: 2 //2秒后自动关闭
                })
                return
            }
            if(!this.user.password) {
                this.layer.open({
                    content: '密码不能为空!'
                    ,skin: 'msg'
                    ,time: 2 //2秒后自动关闭
                })
                return
            }
            if(this.user.password.length <6 || this.user.password.length>20) {
                this.layer.open({
                    content: '密码长度为6~20位!'
                    ,skin: 'msg'
                    ,time: 2 //2秒后自动关闭
                })
                return
            }
            if(!this.repeatPass) {
                this.layer.open({
                    content: '重复密码不能为空!'
                    ,skin: 'msg'
                    ,time: 2 //2秒后自动关闭
                })
                return
            }
            if(this.repeatPass.length<6 || this.repeatPass.length > 20) {
                this.layer.open({
                    content: '重复密码长度为6~20位!'
                    ,skin: 'msg'
                    ,time: 2 //2秒后自动关闭
                })
                return
            }
            if(this.repeatPass !== this.user.password) {
                this.layer.open({
                    content: '两次密码不一致!'
                    ,skin: 'msg'
                    ,time: 2 //2秒后自动关闭
                })
                return
            }
            if(!this.checked) {
                this.layer.open({
                    content: '请勾选微活平台协议!'
                    ,skin: 'msg'
                    ,time: 2 //2秒后自动关闭
                })
                return
            }
            this.compleTxt = '注册中...';
            let _that = this;
            this.user.sign = md5(common.changeString(common.objKeySort(this.user),'431104c0-984c-4415-90ed-847938e5cb00')).toUpperCase();
            api.regUser(this.user)
            .then(res => {
                if(res.data.header.rspCode == 0) {
                    _that.$router.push('/userRegister/userSuccess')
                }
                if(res.data.header.rspCode == 401) {
                    delete _that.user.sign;
                    _that.layer.open({
                        content: res.data.header.rspMsg
                        ,skin: 'msg'
                        ,time: 2 //2秒后自动关闭
                    })
                    _that.compleTxt = '注册';
                }
            }).catch(err => {
                delete _that.user.sign;
                console.log(err);
            })
        }
    }
}
</script>
<style lang="less" scoped>
@import "../../public/common.less";
.user_two {
    .user_info {
        span {
            margin-right: 20px;
            font-size: 26px; /*px*/
        }
        .choose_radio,.invitate_code {
            position: relative;
            .display-flex;
            .justify-content(flex-start);
            .align-items(center);
            height: 81px;
            margin-bottom: 10px;
            padding: 0 20px;
            background: #fff;
            .border(#fff);  /*px*/
            &.active {
                .border(red);   /*px*/
            }
            label {
                width: 120px;
                margin-right: 50px;
                font-size: 26px; /*px*/
            }
            input[type='text'] {
                border: none;
                font-size: 28px; /*px*/
                letter-spacing: 2px;
                background: none;
            }
            input[type="radio"] + label::before {
                content: "\a0"; /*不换行空格*/
                display: inline-block;
                vertical-align: middle;
                font-size: 18px;
                width: 20px;
                height: 20px;
                margin-right: .4em;
                border-radius: 50%;
                border: 1px solid #686464;  /*px*/
                text-indent: .15em;
                line-height: 1; 
            }
            input[type="radio"]:checked + label::before {
                background-color: #686464;  
                background-clip: content-box;
                padding: 5px;
                width: 10px;
                height: 10px;
            }
            input[type="radio"] {
                position: absolute;
                clip: rect(0, 0, 0, 0);
            }
            select {
                height: .8rem;
                padding-left: .15rem;
                padding-right: .5rem;
                box-sizing: border-box;
                border: solid 1px #575556;
                appearance:none;
                -moz-appearance:none;
                -webkit-appearance:none;
                background: url("../../../static/image/xiangxia.png") no-repeat scroll right center transparent;               
                &::-ms-expand { display: none; }
            }

        }
        .ele {
            .display-flex;
            .justify-content(space-between);
            .align-items(center);
            padding: 0 20px 1px;
            .border(#fff);  /*px*/
            background: #fff;
            margin-bottom: 2px; /*px*/
            &:nth-of-type(2) {
                input {
                    background: none;
                }
            }
            &:nth-of-type(4) {
                margin-bottom: 10px;
            }
            input {
                margin: 1px 0;
                height: 80px;
                border: none;
                font-size: 28px; /*px*/
                letter-spacing: 2px;
                background: none;
            }
            .get_code {
                padding: 10px;
                background: none;
                border: 1px solid #000; /*px*/
                .border-radius(8px);
                font-size: 24px; /*px*/
                -webkit-tap-highlight-color: transparent;
            }
            .count_down {
                padding: 10px;
                .border(#d3d3d3);
                .border-radius(8px);
                 
                color: #d3d3d3;
            }
            .delete_btn {
                width: 40px;
                height: 40px;
            }
        }
        .user_agree {
            display: block;
            padding: 40px 20px;
            margin-bottom: 3px;
            .border(#f5f5f5);  /*px*/
            &.active {
                .border(red);   /*px*/
            }
            input[type='checkbox'] {
                width: 24px;
                height: 24px;
                margin-right: 6px;
            }
            a {
                font-size: 24px; /*px*/
                color: @main-color;
            }
        }
    }
    .complete_btn {
        width: 670px;
        height: 82px;
        margin: 0 auto 0;
        .border-radius(16px);
        background: @main-color;
        color: #fff;
        text-align: center;
        line-height: 82px;
        font-size: 32px; /*px*/
    }
    .alert_tips {
        position: fixed;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        background: rgba(0, 0, 0,.6);
        .at_con {
            position: absolute;
            left: 50%;
            top: 50%;
            .transform(-50%,-50%);
            width: 380px;
            height: 200px;
            .border-radius(6px);
            text-align: center;
            background: #fff;
            .err_txt {
                margin: 50px 0;
                font-weight: bold;
            }
            span {
                padding: 8px 15px;
                .border-radius(2px);
                background: @main-color;
                color: #fff;
                letter-spacing: 2px;
            }
        }
    }
}

</style>



