<template>
    <div class="province_wrap" v-if="isShowLocation">
        <p class="pw_title">
            <img src="../../static/image/zuoce.png" @click="hideLocationA" alt="图片">
            <span>地区选择</span>
            <span @click="hideLocationB">确定</span>
        </p>
        <div class="city_list">
            <ul class="pro_det" @click="getProvince">
                <li :class="{'active': index == provinceIndex}" v-for="(province,index) in provinceList" :key="index" @click="selectPro(province,index)">{{province.name}}</li>
            </ul>
             <ul class="city_det">
                <li :class="{'active': index == cityIndex}" v-for="(city,index) in cityList" :key="index" @click="selectCity(city,index)">{{city.name}}</li>
            </ul>
             <ul class="area_det">
                <li :class="{'active': index == areaIndex}" v-for="(area,index) in areaList" :key="index" @click="selectArea(area,index)"><span>{{area.name}}</span></li>
            </ul>
        </div>
    </div>
</template>
<script>
import qs from 'qs';
import api from "@/http/api.js";
import md5 from 'js-md5';
import common from "../public/common.js";  //引入封装的公共方法
export default {
    name: 'provincePage',
    components: {

    },
    props: {
        isShowLocation: {
            type: Boolean,
            default: false
        },
        nullObj: {
            type: Object,
            default: null
        }
    },
    data() {
        return {
            provinceList: [],       //省
            cityList: [],           //市
            areaList: [],           //区
            provinceIndex: -1,      //省当前索引值
            cityIndex: -1,
            areaIndex: -1,
            locationData: {},        //存放省市区的json
            errorTxt: ''
        }
    },
    created() {
        this.getProvince();
        this.locationData = this.nullObj;
    },
    methods: {
        hideLocationB() {
            if(!this.locationData.Province) {
                this.layer.open({
                    content: '请选择省'
                    ,skin: 'msg'
                    ,time: 2 //2秒后自动关闭
                })
                return
            }
            if(!this.locationData.City) {
                this.layer.open({
                    content: '请选择市'
                    ,skin: 'msg'
                    ,time: 2 //2秒后自动关闭
                })
                return
            }
            if(!this.locationData.Area) {
                this.layer.open({
                    content: '请选择区'
                    ,skin: 'msg'
                    ,time: 2 //2秒后自动关闭
                })
                return
            }
            this.$emit('hideLocationHandleB',false,this.locationData);
            this.provinceIndex = -1
            this.cityList = []
            this.areaList = []
        },
        hideLocationA() {
            this.$emit('hideLocationHandleA',false);
        },
        // 获取省
        getProvince() {
            let info = {
                sign: ''
            },
            _that = this;
            info.sign = md5(common.changeString({},'431104c0-984c-4415-90ed-847938e5cb00')).toUpperCase();
            
            api.getProvince(info)
            .then(res => {
                if(res.data.header.rspCode == 0) {
                    _that.provinceList = res.data.body;
                }
                if(res.data.header.rspCode == 401) {
                    _that.layer.open({
                        content: res.data.header.rspMsg
                        ,skin: 'msg'
                        ,time: 2 //2秒后自动关闭
                    })
                }
            }).catch(err => {
                console.log(err);
            })
        },
        // 点击省
        selectPro(province,index) {
            // 请求class=active的省下面的市所需数据
            let info = {
                province_id: province.id
            },
            _that = this;
            this.provinceIndex = index;
            this.cityIndex = -1;            //点击省  市的index=-1 
            this.areaIndex = -1;            //点击省  区的index=-1 
            this.areaList = [];             //点击省  清空市
            this.locationData.Province = province.name;
            this.locationData.City = '';
            this.locationData.Area = '';

            info.sign = md5(common.changeString(common.objKeySort(info),'431104c0-984c-4415-90ed-847938e5cb00')).toUpperCase();

            api.getCity(info)
            .then(res => {
                if(res.data.header.rspCode == 0) {
                    _that.cityList = res.data.body;
                }
                if(res.data.header.rspCode == 401) {
                    _that.layer.open({
                        content: res.data.header.rspMsg
                        ,skin: 'msg'
                        ,time: 2 //2秒后自动关闭
                    })
                }
            }).catch(err => {
                console.log(err);
            })
        },
        // 点击市
        selectCity(city,index) {
            let info = {
                'city_id': city.id
            },
            _that = this;
            this.cityIndex = index;
            this.areaIndex = -1;
            this.locationData.City = city.name;
            this.locationData.Area = '';
            
            info.sign = md5(common.changeString(common.objKeySort(info),'431104c0-984c-4415-90ed-847938e5cb00')).toUpperCase();
            api.getArea(info)
            .then(res => {
                if(res.data.header.rspCode == 0) {
                    _that.areaList = res.data.body;
                }
                if(res.data.header.rspCode == 401) {
                    _that.layer.open({
                        content: res.data.header.rspMsg
                        ,skin: 'msg'
                        ,time: 2 //2秒后自动关闭
                    })
                }
            }).catch(err => {
                console.log(err);
            })
        },
        // 点击区
        selectArea(area,index) {
            this.areaIndex = index;
            this.locationData.Area = area.name;
        }
    }
}
</script>
<style lang="less" scoped>
@import "../public/common.less";
.province_wrap {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: #fafafa;
    .pw_title {
        .display-flex;
        .justify-content(space-between);
        .align-items(center);
        height: 80px;
        padding: 0 20px;
        .border-bottom(#e5e5e5);
        img {
            width: 18px;
        }
        span {
            font-size: 32px;  /*px*/
        }
    }
    .city_list {
        .display-flex;
        .justify-content(space-between);
        height: calc(100% - 1.15rem);
        .pro_det,.city_det,.area_det {
            flex:1;            
            overflow-y: auto !important;
            text-align: center;
            &::-webkit-scrollbar{
                display: none
              }
            li {
                width: 100%;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                font-size: 32px;   /* px */
            }
        }
        .pro_det {
            background: #fff;
            li {
                height: 100px;
                line-height: 100px;
                &.active {
                    background: #f8f4f5;
                    color: @second-color;
                }
            }
        }
        .city_det {
            background: #f8f4f5;
            li {
                height: 100px;
                line-height: 100px;
                &.active {
                    background: #fafafa;
                    color: @second-color;
                }
            }
        }
        .area_det {
            padding-top: 20px;
            background: #ebe7e4;
            li {
                margin-bottom: 15px;
                &.active {
                    color: @second-color;
                }
                span {
                    display: inline-block;
                    width: 200px;
                    padding: 7px 7px;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    .border(#e5e5e5);
                    .border-radius(8px);
                    background: #fff;
                }
            }
        }
    }
}
</style>



