import Vue from 'vue'
import Router from 'vue-router'
import chooseType from '@/views/index/chooseType'
import userRegister from '@/views/user/userRegister'
import userService from '@/views/user/userService'
import userSuccess from '@/views/user/userSuccess'
import masterRegister from '@/views/master/masterRegister'
import masterService from '@/views/master/masterService'
import noticeBook from '@/views/master/noticeBook'
import masterSuccess from '@/views/master/masterSuccess'

Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [
        {
            path: '/',
            name: 'chooseType',
            component: chooseType,
            meta: {
                title: '首页',
                keepAlive: false
            }
        },
        {
            path: '/userRegister',
            name: 'userRegister',
            component: userRegister,
            meta: {
                title: '用户注册',
                keepAlive: true
            }
        },
        {
            path: '/userRegister/userService',
            name: 'userService',
            component: userService,
            meta: {
                title: '微活平台协议(用户)',
                keepAlive: false
            }
        },
        {
            path: '/userRegister/userSuccess',
            name: 'userSuccess',
            component: userSuccess,
            meta: {
                title: '注册用户成功',
                keepAlive: false
            }
        },
        {
            path: '/masterRegister',
            name: 'masterRegister',
            component: masterRegister,
            meta: {
                title: '师傅注册',
                keepAlive: true
            }
        },
        {
            path: '/masterRegister/masterService',
            name: 'masterService',
            component: masterService,
            meta: {
                title: '微活平台协议(师傅)',
                keepAlive: false
            }
        },
        {
            path: '/masterRegister/noticeBook',
            name: 'noticeBook',
            component: noticeBook,
            meta: {
                title: '安全告知书',
                keepAlive: false
            }
        },
        {
            path: '/masterRegister/masterSuccess',
            name: 'masterSuccess',
            component: masterSuccess,
            meta: {
                title: '师傅注册成功',
                keepAlive: false
            }
        },
        {
            path: '*',
            redirect: '/'
        }
    ]
})



