                <p>【海量信息】微活平台仅向您提供微活平台服务，您了解微活平台上的信息系服务商自行发布，且可能存在风险和瑕疵。
                    <span class="line">微活平台不对服务所涉及的技术的质量、 稳定、完整及对服务商信息的有效性、准确性、合法性、真实性、及时性做出任何承诺和保证，并且不承担任何由此引起的法律责任。</span>
                    在任何情况下，用户应充分了解交易风险后，依据自身风险承受能力和经验，衡量平台披露的交易内容及交易对方信息的准确性、真实性以及合法性，微活平台对用户因其选择微活平台的服务而导致的任何直接、间接损失（ 包括但不限于交易损失、资金损失、利润损失、营业中断损失等）均不承担法律责任，均由用户自行承担。
                </p>
                <p>【调处决定】您理解并同意，在争议调处服务中，微活平台的维权专员、大众评审员并非专业人士，仅能以普通人的认知对用户提交的凭证进行判断。因此，
                    <span class="line">除存在故意或重大过失外，调处方对争议调处决定免责。</span>
                </p>
            </div>
            <div class="five paragraph">
                <span class="sort">五、用户信息的保护及授权</span>
                <span class="sort">5.1个人信息的保护</span>
                <p>微活平台非常重视用户个人信息（即能够独立或与其他信息结合后识别用户身份的信息）的保护，在您使用微活平台提供的服务时，您同意微活平台按照在微活平台上公布的隐私权政策收集、存储、使用、披露和保护您的个人信息。微活平台希望通过隐私权政策向您清楚地介绍微活平台对您个人信息的处理方式，因此微活平台建议您完整地阅读隐私权政策（点击此处或点击微活平台首页底部链接），以帮助您更好地保护您的隐私权。</p>
            </div>
            <div class="five paragraph">
                <span class="sort">5.2非个人信息的保证与授权</span>
                <p>【信息的发布】您声明并保证，您对您所发布的信息拥有相应、合法的权利。否则，
                    <span class="line">微活平台可对您发布的信息依法或依本协议进行删除或屏蔽。</span>
                </p>
                <p>【禁止性信息】
                    <span class="line">您应当确保您所发布的信息不包含以下内容：</span>
                </p>
                <p>
                    <span class="line">（一）违反国家法律法规禁止性规定的；</span>
                </p>
                <p>
                    <span class="line">（二）政治宣传、封建迷信、淫秽、色情、赌博、暴力、恐怖或者教唆犯罪的；</span>
                </p>
                <p>
                    <span class="line">（三）欺诈、虚假、不准确或存在误导性的；</span>
                </p>
                <p>
                    <span class="line">（四）侵犯他人知识产权或涉及第三方商业秘密及其他专有权利的；</span>
                </p>
                <p>
                    <span class="line">（五）侮辱、诽谤、恐吓、涉及他人隐私等侵害他人合法权益的；</span>
                </p>
                <p>
                    <span class="line">（六）存在可能破坏、篡改、删除、影响微活平台任何系统正常运行或未经授权秘密获取微活平台及其他用户的数据、个人资料的病毒、木马、爬虫等恶意软件、程序代码的；</span>
                </p>
                <p>
                    <span class="line">（七）其他违背社会公共利益或公共道德或依据相关微活平台协议、规则的规定不适合在微活平台上发布的。</span>
                </p>
                <p>【授权使用】对于您提供及发布除个人信息外的文字、图片等非个人信息，
                    <span class="line">在版权保护期内您免费授予微活平台获得全球排他的许可使用权利及再授权给其他第三方使用的权利。您同意微活平台存储、使用、复制、修订、编辑、发布、展示、翻译、分发您的非个人信息或制作其派生作品，并以已知或日后开发的形式、媒体或技术将上述信息纳入其它作品内。</span>
                </p>
                <p>为方便您使用微活平台其他相关服务，
                    <span class="line">您授权微活平台将您在账户注册和使用微活平台服务过程中提供、形成的信息传递给微活平台其他相关服务提供者，例如，微活平台可能将该信息发送至信用卡服务商以完成付款程序。或从微活平台其他相关服务提供者获取您在注册、使用相关服务期间提供、形成的信息。</span>
                </p>
            </div>
            <div class="six paragraph">
                <span class="sort">六、 用户的违约及处理</span>
                <span class="sort">6.1 违约认定</span>
                <p>发生如下情形之一的，视为您违约：</p>
                <p>（一）使用微活平台服务时违反有关法律法规规定的；</p>
                <p>（二）违反本协议或本协议补充协议（即本协议第2.2条）约定的。</p>
                <p>为适应平台发展和满足海量用户对高效优质服务的需求，您理解并同意，
                    <span class="line">微活平台可在微活平台规则中约定违约认定的程序和标准。如：微活平台可依据服务商提供的证据来认定您是否构成违约；您有义务对您的指控进行充分举证和合理解释，否则将被认定为违约。</span>
                </p>
            </div>
            <div class="six paragraph">
                <span class="sort">6.2 违约处理措施</span>
                <p>【信息处理】您在微活平台上发布的信息构成违约的，微活平台可根据相应规则立即对相应信息进行删除、屏蔽处理等。</p>
                <p>【行为限制】您在微活平台上实施的行为，或虽未在微活平台上实施但对微活平台及其用户产生影响的行为构成违约的，微活平台
                    <span class="line">可依据相应规则对您执行限制参加商业活动、中止向您提供部分或全部服务、冻结或删除账户等处理措施。</span>
                </p>
                <p>【处理结果公示】
                    <span class="line">微活平台可将对您上述违约行为处理措施信息以及其他经国家行政或司法机关生效法律文书确认的违法信息在微活平台上予以公示。</span>
                </p>
            </div>
            <div class="six paragraph">
                <span class="sort">6.3赔偿责任</span>
                <p>
                    <span class="line">如您的行为使微活平台遭受损失（包括自身的直接经济损失、商誉损失及对外支付的赔偿金、和解款、律师费、诉讼费等间接经济损失），您应赔偿微活平台的上述全部损失。</span>
                </p>
                <p>
                    <span class="line">如您的行为使微活平台遭受第三人主张权利，微活平台可在对第三人承担金钱给付等义务后就全部损失向您追偿。</span>
                </p>
            </div>
            <div class="six paragraph">
                <span class="sort">6.4特别约定</span>
                <p>【商业贿赂】如您向微活平台的雇员或顾问等提供实物、现金、现金等价物、劳务、旅游等价值明显超出正常商务洽谈范畴的利益，则可视为您存在商业贿赂行为。
                    <span class="line">发生上述情形的，微活平台可立即终止与您的所有合作并向您收取违约金及/或赔偿金，</span>
                    该等金额以微活平台因您的贿赂行为而遭受的经济损失和商誉损失作为计算依据。
                </p>
                <p>【关联处理】
                    <span class="line">如您因严重违约导致微活平台终止本协议时，出于维护平台秩序及保护消费者权益的目的，微活平台可对与您在其他协议项下的合作采取中止甚或终止协议的措施，并以本协议第八条约定的方式通知您。</span>
                </p>
                <p>
                    <span class="line">如微活平台与您签署的其他协议及微活平台与您签署的协议中明确约定了对您在本协议项下合作进行关联处理的情形，则微活平台出于维护平台秩序及保护消费者权益的目的，可在收到指令时中止甚至终止协议，并以本协议第八条约定的方式通知您。</span>
                </p>
            </div>
            <div class="seven paragraph">
                <span class="sort">七、协议的变更</span>
                <p>
                    微活平台可根据国家法律法规变化及维护交易秩序、保护消费者权益需要，不时修改本协议、补充协议，变更后的协议、补充协议（下称“变更事项”）将通过法定程序并以本协议第八条约定的方式通知您。 如您不同意变更事项，您有权于变更事项确定的生效日前联系微活平台反馈意见。如反馈意见得以采纳，微活平台将酌情调整变更事项。
                </p>
                <p>
                    <span class="line">如您对已生效的变更事项仍不同意的，您应当于变更事项确定的生效之日起停止使用微活平台服务，变更事项对您不产生效力；如您在变更事项生效后仍继续使用微活平台服务，则视为您同意已生效的变更事项。</span>
                </p>
            </div>
            <div class="eight paragraph">
                <span class="sort">八、通知</span>
                <span class="sort">8.1有效联系方式</span>
                <p>
                    您在注册成为微活平台用户，并接受微活平台服务时，您应该向微活平台提供真实有效的联系方式（包括您的电子邮件地址、联系电话、联系地址等），对于联系方式发生变更的，您有义务及时更新有关信息，并保持可被联系的状态。 如您不同意变更事项，您有权于变更事项确定的生效日前联系微活平台反馈意见。如反馈意见得以采纳，微活平台将酌情调整变更事项。
                </p>
                <p>您在注册微活平台用户时生成的用于登陆微活平台接收站内信、系统消息也作为您的有效联系方式。</p>
                <p>
                    <span class="line">微活平台将向您的上述联系方式的其中之一或其中若干向您送达各类通知，而此类通知的内容可能对您的权利义务产生重大的有利或不利影响，请您务必及时关注。</span>
                </p>
            </div>
            <div class="eight paragraph">
                <span class="sort">8.2 通知的送达</span>
                <p>
                    微活平台通过上述联系方式向您发出通知，其中以电子的方式发出的书面通知，包括但不限于在微活平台公告，向您提供的联系电话发送手机短信，向您提供的电子邮件地址发送电子邮件，在发送成功后即视为送达；以纸质载体发出的书面通知，按照提供联系地址交邮后的第五个自然日即视为送达。
                    如您不同意变更事项，您有权于变更事项确定的生效日前联系微活平台反馈意见。如反馈意见得以采纳，微活平台将酌情调整变更事项。
                </p>
                <p>对于在微活平台上因交易活动引起的任何纠纷，您同意司法机关（包括但不限于人民法院）可以通过手机短信、电子邮件等现代通讯方式或邮寄方式向您送达法律文书（包括但不限于诉讼文书）。您指定接收法律文书的手机号码、电子邮箱等联系方式为您在微活平台注册、更新时提供的手机号码、电子邮箱联系方式，司法机关向上述联系方式发出法律文书即视为送达。您指定的邮寄地址为您的法定联系地址或您提供的有效联系地址。</p>
                <p>
                    您同意司法机关可采取以上一种或多种送达方式向您达法律文书，司法机关采取多种方式向您送达法律文书，送达时间以上述送达方式中最先送达的为准。
                </p>
                <p>您同意上述送达方式适用于各个司法程序阶段。如进入诉讼程序的，包括但不限于一审、二审、再审、执行以及督促程序等。</p>
                <p>你应当保证所提供的联系方式是准确、有效的，并进行实时更新。如果因提供的联系方式不确切，或不及时告知变更后的联系方式，使法律文书无法送达或未及时送达，由您自行承担由此可能产生的法律后果。</p>
            </div>
            <div class="nine paragraph">
                <span class="sort">九、协议的终止</span>
                <span class="sort">9.1 终止的情形</span>
                <p>
                    【用户发起的终止】您有权通过以下任一方式终止本协议：
                </p>
                <p>（一）变更事项生效前您停止使用并明示不愿接受变更事项的；</p>
                <p>（二）您明示不愿继续使用微活平台服务，且符合微活平台终止条件的。</p>
                <p>【微活平台发起的终止】出现以下情况时，微活平台可以本协议第八条的所列的方式通知您终止本协议：</p>
                <p>（一）您违反本协议约定，微活平台依据违约条款终止本协议的；</p>
                <p>（二）您盗用他人账户、发布违禁信息、骗取他人财物、引导线下交易、扰乱市场秩序、采取不正当手段谋利等行为，微活平台依据微活平台规则对您的账户予以查封的；</p>
                <p>（三）除上述情形外，因您多次违反微活平台规则相关规定且情节严重，微活平台依据微活平台规则对您的账户予以查封的；</p>
                <p>（四）您的账户被微活平台依据本协议回收的；</p>
                <p>（五）其它应当终止服务的情况。</p>
            </div>
            <div class="nine paragraph">
                <span class="sort">9.2 协议终止后的处理</span>
                <p>
                    【用户信息披露】
                    <span class="line">本协议终止后，除法律有明确规定外，微活平台无义务向您或您指定的第三方披露您账户中的任何信息。</span>
                </p>
                <p>【微活平台权利】本协议终止后，微活平台仍享有下列权利：</p>

                <p>（一）继续保存您留存于微活平台的本协议第五条所列的各类信息；</p>
                <p>（二）对于您过往的违约行为，微活平台仍可依据本协议向您追究违约责任。</p>

                <p>【交易处理】
                    <span class="line">本协议终止后，对于您在本协议存续期间产生的交易订单，微活平台可通知交易相对方并根据交易相对方的意愿决定是否关闭该等交易订单；如交易相对方要求继续履行的，则您应当就该等交易订单继续履行本协议及交易订单的约定，并承担因此产生的任何损失或增加的任何费用。</span>
                </p>
            </div>
            <div class="ten paragraph">
                <span class="sort">十、法律适用、管辖与其他</span>
                <p>
                    【法律适用】
                    <span class="line">本协议之订立、生效、解释、修订、补充、终止、执行与争议解决均适用中华人民共和国大陆地区法律；如法律无相关规定的，参照商业惯例及/或行业惯例。</span>
                </p>
                <p>【管辖】
                    <span class="line">您因使用微活平台服务所产生及与微活平台服务有关的争议，由微活平台与您协商解决。协商不成时，任何一方均可向苏州市仲裁委员会提起仲裁。</span>
                </p>
                <p>【可分性】
                    <span class="line">本协议任一条款被视为废止、无效或不可执行，该条应视为可分的且并不影响本协议其余条款的有效性及可执行性。</span>
                </p>
            </div>
        </div>
    </div>
</template>
