<template>
    <div class="master_two">
        <div class="master_info">
            <div class="invitate_code">
                <span>推荐人:</span>
                <input type="text" placeholder="邀请码" disabled v-model="master.beInviteCode">
            </div>
            <div class="choose_radio" @click="showLocation">
                <span>所在区域:</span>
                <span>{{locationName}}</span>
            </div>
            <div class="ele">
                <input type="text" maxlength="11" v-model.trim="master.phone" placeholder="请输入您的手机号(必填)">
                <button class="get_code" @click="getCode" v-if="!showTime">{{getCodeText}}</button>
                <button class="get_code" v-else>{{getCodeText}}</button>
            </div>
            <div class="ele">
                <input type="text" maxlength="6" v-model.trim="master.code" placeholder="请输入您的验证码(必填)">
            </div>
            <div class="ele">
                <input type="password" v-model.trim="master.password" placeholder="请输入您的密码(必填)">
                <img class="delete_btn" @click="deletePass" src="../../../static/image/delete.png" alt="图片">
            </div>
            <div class="ele">
                <input type="password" v-model.trim="repeatPass" placeholder="请确认您的密码(必填)">
                <img class="delete_btn" @click="deleteRepass" src="../../../static/image/delete.png" alt="图片">
            </div>
            <label for="agree" class="master_agree" >
                <input type="checkbox" id="agree" @click="changeChecked(checked)" v-model="checked">阅读并同意
                <router-link to="/masterRegister/masterService">《微活平台协议》</router-link>
                <router-link to="/masterRegister/noticeBook">《人生安全风险告知书》</router-link>
            </label>
        </div>
        <p class="complete_btn" v-if="compleTxt == '注册中...'" >{{compleTxt}}</p>
        <p class="complete_btn" v-else @click="completeReg">{{compleTxt}}</p>
        <citySelect :isShowLocation = isShowLocation 
                    v-on:hideLocationHandleA="hideLocationEventA"
                    v-on:hideLocationHandleB="hideLocationEventB"
                    :nullObj = 'nullObj'
        ></citySelect>
        <div class="alert_tips" v-if="hideTips">
            <div class="notice_book">
                <div class="nb_title">
                    <em></em>
                    <span>人身安全风险告知书</span>
                    <img @click="close_tips" src="static/image/close_icon.png" alt="">
                </div>
                <div class="text_det">
                    <p class="page_des_one">尊敬的工人师傅：</p>
                    <p class="page_des_one">您好！</p>
                    <p class="page_des_one">鉴于：</p>
                    <p class="big_sentence">1、 装修行业面临各式各样的意外风险；</p>
                    <p class="big_sentence">2、装修业主（即在微活工匠平台发布任务的用户）希望为自己提供服务的工人，多一层安全保护屏障；</p>
                    <p class="big_sentence">
                    3、“微活工匠”平台仅系一个为工人师傅提供服务交易的平台，是微活工匠平台用户发布任务和工人提供服务的交易市场，与工人师傅您不存在任何劳动合同或雇佣法律关系，对于您在本平台因提供服务可能发生的一切人身安全损害，本平台不承担任何赔偿责任或连带赔偿责任。
                    为提高师傅您的安全意识，我们微活工匠平台从师傅您自身安全的角度出发，谨慎并郑重建议您购买一份意外伤害险，为自己及家人的生活，增添一份安全保障！
                    若您尚未购买意外险，可能会影响您即将在微活工匠平台上的有效接单，且一旦您在提供服务过程前、过程中、过程后发生人身意外伤害，微活工匠平台将对此不承担任何赔偿责任或连带赔偿责任。
                    </p>
                </div>
                <p class="already_understand" @click="registerHandle">我已了解</p>
            </div>
        </div>
    </div>
</template>
<script>
import qs from 'qs';
import citySelect from '@/components/citySelect'
import md5 from 'js-md5';
import common from "../../public/common.js";  //引入封装的公共方法
import api from "@/http/api.js";
export default {
    name: 'masterRegisterPage',
    components: {
        citySelect
    },
    data() {
        return {
            isShowLocation: false,                    //是否显示省市区             
            locationName: '请选择您所在的省市区',       //省市区
            showTime: false,
            time: 60,                                 //倒计时时间
            getCodeText: '获取验证码',
            provinceText: '请选择省市区',
            compleTxt: '注册',
            repeatPass: '',                     //重复密码
            checked: false,                      //微活协议
            master: {
                beInviteCode: '',               //邀请码
                phone: '',                      //电话
                code: '',                       //验证码
                password: '',                   //密码,
                province: '',                   //省
                city: '',                       //市
                area: ''                        //区
            },
            info: {
                sign:'431104c0-984c-4415-90ed-847938e5cb00'
            },
            nullObj: {},                         //清空省市区
            hideTips: false,                     //是否隐藏风险告知书
            isRegistering: false
        }
    },
    created() {
        this.master.beInviteCode = this.$route.query.code;
    },
    methods: {
        hideLocationEventA() {
            this.isShowLocation = false
        },
        hideLocationEventB(a,b) {
            this.nullObj = b;
            this.isShowLocation = a
            this.locationName = ''
            for(var i in b) {
                this.locationName += ' '+b[i];
            }
            this.master.province = b.Province
            this.master.city = b.City
            this.master.area = b.Area
        },
        showLocation() {    //省市区选择
            this.isShowLocation = true;
            for(var i in this.nullObj) {
                delete(this.nullObj[i])
            }
        }, 
        deletePass() {
            this.master.password = ''
        },
        deleteRepass() {
            this.repeatPass = ''
        },
        getCode() {
            let info = {
                phone: this.master.phone
            },
            _that = this;
            info.sign = md5(common.changeString(common.objKeySort(info),'431104c0-984c-4415-90ed-847938e5cb00')).toUpperCase();

            //验证所填手机号
            if(!this.master.phone) {
                this.layer.open({
                    content: '手机号码不能为空!'
                    ,skin: 'msg'
                    ,time: 2 //2秒后自动关闭
                })
                return 
            }
            if(!common.checkPhone(this.master.phone)) {
                this.layer.open({
                    content: '手机号码格式不正确!'
                    ,skin: 'msg'
                    ,time: 2 //2秒后自动关闭
                })
                return
            }
            this.getCodeText = '发送中...'
            this.showTime = true
           //发送请求

            api.sendRegisterSmsM(info)
            .then(res => {
                if(res.data.header.rspCode == 0) {
                    let timer = setInterval(() =>{
                        _that.time--;
                        _that.getCodeText = _that.time+'S后重新获取'
                        if(_that.time < 1) {
                            _that.time = 60
                            _that.showTime = false
                            _that.getCodeText = '重新获取验证码'
                            clearInterval(timer)
                        }
                    },1000)
                }
                if(res.data.header.rspCode == 401) {
                    _that.layer.open({
                        content: res.data.header.rspMsg
                        ,skin: 'msg'
                        ,time: 2 //2秒后自动关闭
                    })
                    _that.getCodeText = '获取验证码'
                    _that.showTime = false
                }
            }).catch(err => {
                console.log(err);
            })
        },
        // 勾选用户协议
        changeChecked(item) {
            if(item) {
                item = false
            }else {
                item = true
            }
        },
        // 点击完成按钮
        completeReg() {
            if(!this.master.province) {
                this.layer.open({
                    content: '请选择省!'
                    ,skin: 'msg'
                    ,time: 2 //2秒后自动关闭
                })
                return
            }
            if(!this.master.city) {
                this.layer.open({
                    content: '请选择市!'
                    ,skin: 'msg'
                    ,time: 2 //2秒后自动关闭
                })
                return
            }
            if(!this.master.area) {
                this.layer.open({
                    content: '请选择区!'
                    ,skin: 'msg'
                    ,time: 2 //2秒后自动关闭
                })
                return
            }
            if(!this.master.phone) {
                this.layer.open({
                    content: '手机号码不能为空!'
                    ,skin: 'msg'
                    ,time: 2 //2秒后自动关闭
                })
                return
            }
            if(!this.master.code) {
                this.layer.open({
                    content: '验证码不能为空!'
                    ,skin: 'msg'
                    ,time: 2 //2秒后自动关闭
                })
                return
            }
            if(!this.master.password) {
                this.layer.open({
                    content: '密码不能为空!'
                    ,skin: 'msg'
                    ,time: 2 //2秒后自动关闭
                })
                return
            }
            if(this.master.password.length <6 || this.master.password.length>20) {
                this.layer.open({
                    content: '密码长度为6~20位!'
                    ,skin: 'msg'
                    ,time: 2 //2秒后自动关闭
                })
                return
            }
            if(!this.repeatPass) {
                this.layer.open({
                    content: '重复密码不能为空!'
                    ,skin: 'msg'
                    ,time: 2 //2秒后自动关闭
                })
                return
            }
            if(this.repeatPass.length<6 || this.repeatPass.length > 20) {
                this.layer.open({
                    content: '重复密码长度为6~20位!'
                    ,skin: 'msg'
                    ,time: 2 //2秒后自动关闭
                })
                return
            }
            if(this.repeatPass !== this.master.password) {
                this.layer.open({
                    content: '两次密码不一致!'
                    ,skin: 'msg'
                    ,time: 2 //2秒后自动关闭
                })
                return
            }
            if(!this.checked) {
                this.layer.open({
                    content: '请勾选微活平台协议!'
                    ,skin: 'msg'
                    ,time: 2 //2秒后自动关闭
                })
                return
            }

            this.compleTxt = '注册中...';
            this.hideTips = true;
        },
        // 关闭风险告知书
        close_tips() {
            this.hideTips = false
        },
        // 点击我已了解 开始注册
        registerHandle() {
            if(this.isRegistering) return
            let _that = this;
            this.master.sign = md5(common.changeString(common.objKeySort(this.master),'431104c0-984c-4415-90ed-847938e5cb00')).toUpperCase();
            this.isRegistering = true;
            api.masterRegister(this.master)
            .then(res => {
                if(res.data.header.rspCode == 0) {
                    _that.$router.push('/masterRegister/masterSuccess')
                }
                if(res.data.header.rspCode == 401) {
                    delete this.master.sign;
                    _that.isRegistering = false;
                    _that.layer.open({
                        content: res.data.header.rspMsg
                        ,skin: 'msg'
                        ,time: 2 //2秒后自动关闭
                    })
                    _that.compleTxt = '注册';
                }
            }).catch(err => {
                _that.isRegistering = false;
                delete _that.master.sign;
                console.log(err);
            })
        }
    }
}
</script>
<style lang="less" scoped>
@import "../../public/common.less";
.master_two {
    width: 100%;
    height: 100%;
    background: #F5F5F5;
    .master_info {
        span {
            margin-right: 20px;
            font-size: 26px; /*px*/
        }
        .choose_radio,.invitate_code {
            position: relative;
            .display-flex;
            .justify-content(flex-start);
            .align-items(center);
            height: 81px;
            margin-bottom: 10px;
            padding: 0 20px;
            background: #fff;
            .border(#fff);  /*px*/
            &.active {
                .border(red);   /*px*/
            }
            label {
                width: 120px;
                margin-right: 50px;
                font-size: 26px; /*px*/
            }
            input[type='text'] {
                border: none;
                font-size: 28px; /*px*/
                letter-spacing: 2px;
                background: none;
            }
            input[type="radio"] + label::before {
                content: "\a0"; /*不换行空格*/
                display: inline-block;
                vertical-align: middle;
                font-size: 18px;
                width: 20px;
                height: 20px;
                margin-right: .4em;
                border-radius: 50%;
                border: 1px solid #686464;  /*px*/
                text-indent: .15em;
                line-height: 1; 
            }
            input[type="radio"]:checked + label::before {
                background-color: #686464;  
                background-clip: content-box;
                padding: 5px;
                width: 10px;
                height: 10px;
            }
            input[type="radio"] {
                position: absolute;
                clip: rect(0, 0, 0, 0);
            }
            select {
                height: .8rem;
                padding-left: .15rem;
                padding-right: .5rem;
                box-sizing: border-box;
                border: solid 1px #575556;
                appearance:none;
                -moz-appearance:none;
                -webkit-appearance:none;
                background: url("../../../static/image/xiangxia.png") no-repeat scroll right center transparent;               
                &::-ms-expand { display: none; }
            }

        }
        .ele {
            .display-flex;
            .justify-content(space-between);
            .align-items(center);
            padding: 0 20px 1px;
            .border(#fff);  /*px*/
            background: #fff;
            margin-bottom: 2px; /*px*/
            &:nth-of-type(2) {
                input {
                    background: none;
                }
            }
            &:nth-of-type(4) {
                margin-bottom: 10px;
            }
            input {
                margin: 1px 0;
                height: 80px;
                border: none;
                font-size: 28px; /*px*/
                letter-spacing: 2px;
                background: none;
            }
            .get_code {
                padding: 10px;
                background: none;
                border: 1px solid #000; /*px*/
                .border-radius(8px);
                font-size: 24px; /*px*/
                -webkit-tap-highlight-color: transparent;
            }
            .count_down {
                padding: 10px;
                .border(#d3d3d3);
                .border-radius(8px);
                 
                color: #d3d3d3;
            }
            .delete_btn {
                width: 40px;
                height: 40px;
            }
        }
        .master_agree {
            display: block;
            padding: 40px 20px;
            margin-bottom: 3px;
            .border(#f5f5f5);  /*px*/
            &.active {
                .border(red);   /*px*/
            }
            input[type='checkbox'] {
                width: 24px;
                height: 24px;
                margin-right: 6px;
            }
            a {
                font-size: 24px; /*px*/
                color: #4AAB80;
            }
        }
    }
    .complete_btn {
        width: 670px;
        height: 82px;
        margin: 0 auto 0;
        .border-radius(16px);
        background: #4AAB80;
        color: #fff;
        text-align: center;
        line-height: 82px;
        font-size: 32px; /*px*/
    }
    .alert_tips {
        position: fixed;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        background: rgba(0, 0, 0,.6);
        .notice_book {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%,-50%);
            width: 600px;
            height: 760px;
            background: #fff;
            .nb_title {
                .display-flex;
                .justify-content(space-between);
                .align-items(center);
                height: 100px;
                padding: 0 20px;
                border-bottom: 2px solid #dfdfdf;
                span {
                    font-size: 26px;
                    color: #333;
                }
                img {
                    width: 35px;
                }
            }
            .text_det {
                height: 540px;
                padding: 15px 20px;
                overflow-y: auto;
                .rw_title {
                    text-align: center;
                    color: #404040;
                    font-size: 48px;
                }
                .page_des_one {
                    font-size: 28px;
                }
                .big_sentence {
                    text-indent: 2em;
                    letter-spacing: 8px;
                    font-size: 26px;
                }
            }
            .already_understand {
                height: 100px;
                background: #4AAB80;
                text-align: center;
                line-height: 100px;
                font-size: 28px;
                letter-spacing: 4px;
                color: #fff;
            }
        }
    }
}

</style>



