<template>
    <div class="success">
        <img @click="downApp" src="../../../static/image/user_success_logo.png" alt="图片">
    </div>
</template>
<script>
export default {
    name: 'successPage',
    methods: {
        downApp() {
            var ua = window.navigator.userAgent.toLowerCase();
            var url_ios_download = 'https://itunes.apple.com/cn/app/id1314755897?mt=8';
            var url_android_download =
                'http://download.weihuogongjiang.com/weihuouser.apk';

            //通过window.navigator.userAgent来判断当前设备是IOS还是Android。
            // 微信内置浏览器
            if (ua.match(/MicroMessenger/i) == 'micromessenger') {
                location.href = 'https://android.myapp.com/myapp/detail.htm?apkName=com.miguo.miguo&ADTAG=mobile'
                //非微信浏览器  
            } else {
                if (navigator.userAgent.match(/(iPhone|iPod|iPad);?/i)) {
                    var loadTime = new Date();
                    setTimeout(function () {
                        var outTime = new Date()
                        if (outTime - loadTime > 800) {
                            location.href = url_ios_download;
                        }
                    }, 1000)
                } else if (navigator.userAgent.match(/android/i)) {
                    var loadTime = new Date();
                    setTimeout(function () {
                        location.href = url_android_download;
                    }, 200)
                }
            }
        }
    }
}
</script>
<style lang="less" scoped>
.success {
    width: 100%;
    height: 100%;
    background: #c78074;
    img {
        width: 100%;
    }
}
</style>



